import React, { useState } from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import { useTheme } from './ThemeProvider';
import { useInView } from 'react-intersection-observer';

const Section = styled.section`
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: ${props => props.theme.background};
  color: ${props => props.theme.text};
  padding: 4rem 0;
`;

const Container = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
`;

const SectionTitle = styled(motion.h2)`
  font-size: 3rem;
  text-align: center;
  margin-bottom: 3rem;
  background: ${props => props.theme.gradient};
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
`;

const ProjectsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
`;

const ProjectCard = styled(motion.div)`
  background: ${props => `${props.theme.primary}10`};
  border-radius: 20px;
  overflow: hidden;
  border: 2px solid ${props => `${props.theme.primary}20`};
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 40px ${props => props.theme.primary}20;
  }
`;

const ProjectContent = styled.div`
  padding: 2rem;
`;

const ProjectTitle = styled.h3`
  margin-bottom: 1rem;
  color: ${props => props.theme.primary};
`;

const ProjectDescription = styled.p`
  margin-bottom: 1.5rem;
  line-height: 1.6;
  opacity: 0.9;
`;

const TechTags = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
`;

const TechTag = styled.span`
  padding: 0.3rem 0.8rem;
  background: ${props => props.theme.gradient};
  color: white;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
`;

// New: Dad joke UI
const JokeContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-top: 2.5rem;
  text-align: center;
`;

const JokeButton = styled(motion.button)`
  padding: 0.9rem 1.4rem;
  border-radius: 28px;
  border: none;
  background: ${props => props.theme.gradient};
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 10px 25px ${props => props.theme.primary}30;

  &:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }
`;

const JokeText = styled(motion.p)`
  max-width: 800px;
  line-height: 1.6;
  opacity: 0.9;
`;

const Projects = () => {
  const { ref, inView } = useInView({ threshold: 0.3 });
  const [joke, setJoke] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // Prefer short, punchy jokes
  const curatedShortJokes = [
    "I'm reading a book on anti-gravity. It's impossible to put down.",
    "I used to hate facial hair... but then it grew on me.",
    "Why did the scarecrow win an award? He was outstanding in his field.",
    "I would tell you a joke about construction, but I'm still working on it.",
    "I ordered a chicken and an egg from Amazon. I'll let you know.",
    "I don't trust stairs. They're always up to something.",
    "What do you call fake spaghetti? An impasta.",
    "I used to play piano by ear, now I use my hands."
  ];

  const pickShortJoke = (results = []) => {
    const MAX_LEN = 110; // keep it short
    const short = results.filter(j => (j.joke || '').length > 10 && (j.joke || '').length <= MAX_LEN);
    const pool = short.length ? short : results.sort((a,b) => (a.joke||'').length - (b.joke||'').length).slice(0, 5);
    if (!pool.length) return '';
    return pool[Math.floor(Math.random() * pool.length)].joke.trim();
  };

  const fetchJoke = async () => {
    setError('');
    setLoading(true);
    try {
      // Try search to get multiple options and pick a short one
      const page = Math.floor(Math.random() * 25) + 1;
      const res = await fetch(`https://icanhazdadjoke.com/search?limit=30&page=${page}` , {
        headers: { Accept: 'application/json' }
      });
      if (res.ok) {
        const data = await res.json();
        const selected = pickShortJoke(data.results || []);
        if (selected) {
          setJoke(selected);
          return;
        }
      }
      // Fallback to single joke JSON
      const single = await fetch('https://icanhazdadjoke.com/', {
        headers: { Accept: 'application/json' }
      });
      if (single.ok) {
        const data = await single.json();
        const txt = (data && data.joke) ? data.joke.trim() : '';
        if (txt && txt.length <= 120) {
          setJoke(txt);
          return;
        }
      }
      // Final fallback to curated short jokes
      setJoke(curatedShortJokes[Math.floor(Math.random() * curatedShortJokes.length)]);
    } catch (e) {
      // Network or parsing issues  curated short joke
      setJoke(curatedShortJokes[Math.floor(Math.random() * curatedShortJokes.length)]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Section id="projects" ref={ref}>
      <Container>
        <SectionTitle
          initial={{ opacity: 0, y: 30 }}
          animate={inView ? { opacity: 1, y: 0 } : { opacity: 0, y: 30 }}
        >
          Featured Projects
        </SectionTitle>
        <ProjectsGrid>
          {projects.map((project, index) => (
            <ProjectCard
              key={index}
              initial={{ opacity: 0, y: 50 }}
              animate={inView ? { opacity: 1, y: 0 } : { opacity: 0, y: 50 }}
              transition={{ delay: index * 0.2 }}
              whileHover={{ scale: 1.02 }}
            >
              <ProjectContent>
                <ProjectTitle>{project.title}</ProjectTitle>
                <ProjectDescription>{project.description}</ProjectDescription>
                <TechTags>
                  {project.technologies.map((tech, techIndex) => (
                    <TechTag key={techIndex}>{tech}</TechTag>
                  ))}
                </TechTags>
              </ProjectContent>
            </ProjectCard>
          ))}
        </ProjectsGrid>

        {/* Dad joke CTA */}
        <JokeContainer>
          <JokeButton
            onClick={fetchJoke}
            disabled={loading}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.97 }}
          >
            {loading ? 'Loading' : 'Would you like to hear a dad joke?'}
          </JokeButton>
          {(joke || error) && (
            <JokeText
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              role="status"
              aria-live="polite"
            >
              {error || joke}
            </JokeText>
          )}
        </JokeContainer>
      </Container>
    </Section>
  );
};

export default Projects;
